export default {
	name:'manipulate',
	data() {
		return {
			activeIndex:0
		}
	},
	methods: {
		itemClick(index) {
			this.activeIndex = index;
			this.$emit("activeUpdate",this.activeIndex)
		},
		toHistory() {
			uni.navigateTo({
				url:'../../../../order/order'
			})
		}
	}
}
